@import url(https://fonts.googleapis.com/css?family=K2D);
* {
    font-family: K2D;
}

body {
    background: #333;
    margin: 0;
    padding: 0;
    /* font-family: "Audiowide", -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif; */
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}
.App {
    text-align: center;
    background-color: #555;
    color: #ccc;
}

.App .pane {
    background-color: #555;
    /* border: 1px solid #faa; */
    background-image: linear-gradient( #777, #555);
    box-shadow: 14px 10px 43px 3px rgba(0, 0, 0, 0.6);
}

.App .row .pane-title {
    text-transform: uppercase;
    padding: 0 1rem;
    margin-bottom: 0.5rem;
    background-color: #f55;
    text-align: left;
    border-radius: 5px 5px 0 0;
}

.App .container {
    margin: 10px 0;
    padding-bottom: 10px;
    border-radius: 5px;
    /* padding-top: 10px; */
}

.Planner {
    font-size: 80%;
    /* background-color: #777; */
}

.Planner .locked {
    background-color: #6c757d
}

.Planner .step {
    cursor: pointer;
    background-color: #6c757d;
    margin-bottom: 1px
}

.Planner .current-step {
    background-color: #f55;
}


